*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  
  .main{
    display: grid;
    background-color: aliceblue;
    width: 370px;
    padding: 35px;
  }
  .wrapper{
      display: grid;
      align-items: center;
      justify-content: center;
      background-size:contain;
      height: 100vh;
      background:  url('./images/bg.jpeg');
  }
  .signupbtn{
    display: flex;
    padding: 25px;
    justify-content: center;
  }
  .loginhere{
    padding-left: 5px;
  }
  .account{
    text-align: center;
  }
  .email,.password,.cnfmpassword{
   padding-top: 30px;
  }
  .email{
    padding-bottom: 15px;
  }
  .valid{
    border: solid 1px green;
  }
  .invalid{
    border: solid 1px red;
  }
  .btn{
    border: none;

  }