*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  
  .main-container {
    display: grid;
    background-color: aliceblue;
    width: 370px;
    padding: 35px;
    height: 70%;
  }
  .wrapped{
      display: grid;
      align-items: center;
      justify-content: center;
      background-size:70%;
      height: 100vh;
      background:  url('./images/bg.jpeg');
  }
  .loginbtn{
    display: flex;
    padding: 15px;
    justify-content: center;
    margin-top: -10px;
  }

  .valid{
    border: solid 1px green;
  }
  .invalid{
    border: solid 1px red;
  }
  
  hr{
    border: solid 1px grey;
  }
 
  #backbtn{
   margin-right: 40px;
  }
  .emailcontainer{
    margin-top: -20px;
  }
  .passwordcontainer{
    margin-top: 7px;
  }
  .accountsignup{
    text-align: center;
  }